function Line(i) {
  this.i = i;
  this.spring = random(0.02, 0.1);
  this.spring = 0.02;
  this.friction = random(0.7, 0.95) * 0 + 0.85;
  this.mouseVelocity = new THREE.Vector3();
  // this.mouseOffset = new THREE.Vector3().addScalar(random(-1, 1) * 0.02);
  // this.mouseOffset.z = 0;
  // this.mouseOffset = new THREE.Vector3();
  this.strokeWeight = random(20, 50) * 0 + 200;

  this.points = [];
  this.position = new Float32Array(parameters.count.points * 3 * 2);
  this.next = new Float32Array(parameters.count.points * 3 * 2);
  this.prev = new Float32Array(parameters.count.points * 3 * 2);
  const side = new Float32Array(parameters.count.points * 1 * 2);
  const uv = new Float32Array(parameters.count.points * 2 * 2);
  const index = new Uint16Array((parameters.count.points - 1) * 3 * 2);

  for (let i = 0; i < parameters.count.points; i++) {
    side.set([-1, 1], i * 2);

    const v = i / (parameters.count.points - 1);
    uv.set([0, v, 1, v], i * 4);

    if (i === parameters.count.points - 1) continue;
    const ind = i * 2;
    index.set([ind + 0, ind + 1, ind + 2], (ind + 0) * 3);
    index.set([ind + 2, ind + 1, ind + 3], (ind + 1) * 3);
  }

  this.tmp = new THREE.Vector3();

  // this.color = new THREE.Color(...palette(i / parameters.count.lines));
  // this.color2 = new THREE.Color(...palette(i / parameters.count.lines + 0.2));
  // const wav = i/parameters.count.lines * 200 + 450
  // this.color = new THREE.Color(...zuc(wav))

  for (let i = 0; i < parameters.count.points; i++) {
    this.points[i] = new THREE.Vector3();
  }

  this.geometry = new THREE.BufferGeometry();
  this.geometry.setAttribute(
    "position",
    new THREE.BufferAttribute(this.position, 3)
  );
  this.geometry.setAttribute("next", new THREE.BufferAttribute(this.next, 3));
  this.geometry.setAttribute("prev", new THREE.BufferAttribute(this.prev, 3));
  this.geometry.setAttribute("side", new THREE.BufferAttribute(side, 1));
  this.geometry.setAttribute("uv", new THREE.BufferAttribute(uv, 2));
  this.geometry.setAttribute("index", new THREE.BufferAttribute(index, 1));
  // To draw all triangles within strip need to set indexes for each point
  this.geometry.setIndex(new THREE.BufferAttribute(index, 1));

  // Material
  this.material = new THREE.ShaderMaterial({
    depthWrite: true,
    // wireframe: true,
    // blending: THREE.AdditiveBlending,
    // vertexColors: true, // adds custom color attribute
    vertexShader: vertex,
    fragmentShader: fragment,
    transparent: true,
    uniforms: {
      uTime: { value: 0 },
      uResolution: {
        value: new THREE.Vector4(sizes.width, sizes.height, 1, 1),
      },
      uStrokeWeight: { value: this.strokeWeight },
      // uColor: { value: this.color },
      // uColor2: { value: this.color2 },
      uCount: { value: parameters.count.points },
      PI: { value: Math.PI },
    },
  });

  // Line
  this.line = new THREE.Mesh(this.geometry, this.material);
  scene.add(this.line);

  this.updateGeometry = () => {
    this.points.forEach((p, i) => {
      p.toArray(this.position, i * 3 * 2);
      p.toArray(this.position, i * 3 * 2 + 3);

      if (!i) {
        // If first point, calculate prev using the distance to 2nd point
        this.tmp
          .copy(p)
          .sub(this.points[i + 1])
          .add(p);
        this.tmp.toArray(this.prev, i * 3 * 2);
        this.tmp.toArray(this.prev, i * 3 * 2 + 3);
      } else {
        p.toArray(this.next, (i - 1) * 3 * 2);
        p.toArray(this.next, (i - 1) * 3 * 2 + 3);
      }

      if (i === this.points.length - 1) {
        // If last point, calculate next using distance to 2nd last point
        this.tmp
          .copy(p)
          .sub(this.points[i - 1])
          .add(p);
        this.tmp.toArray(this.next, i * 3 * 2);
        this.tmp.toArray(this.next, i * 3 * 2 + 3);
      } else {
        p.toArray(this.prev, (i + 1) * 3 * 2);
        p.toArray(this.prev, (i + 1) * 3 * 2 + 3);
      }
    });

    // for (let i = 0; i < parameters.count.points; i++) {
    //   const j = i * 3 * 2
    //   const j3 = i * 3 * 2 + 3

    //   // Position
    //   // this.position[j + 0] = this.points[i].x
    //   // this.position[j + 1] = this.points[i].y
    //   // this.position[j + 2] = this.points[i].z
    //   this.position[j + 0] = this.position[j3 + 0] = this.points[i].x
    //   this.position[j + 1] = this.position[j3 + 0] = this.points[i].y
    //   this.position[j + 2] = this.position[j3 + 0] = this.points[i].z

    //   // Side
    //   this.side.set([-1, 1], i * 2)
    // }

    // for (let i = 0; i < parameters.count.points; i++) {
    //   const j = i * 3
    //   const k = i === parameters.count.points - 1 ? i * 3 : (i + 1) * 3
    //   const l = i === 0 ? i * 3 : (i - 1) * 3

    //   // Next
    //   this.next[j + 0] = this.position[k + 0]
    //   this.next[j + 1] = this.position[k + 1]
    //   this.next[j + 2] = this.position[k + 2]

    //   // Prev
    //   this.prev[j + 0] = this.position[l + 0]
    //   this.prev[j + 1] = this.position[l + 1]
    //   this.prev[j + 2] = this.position[l + 2]
    // }

    this.geometry.attributes.position.needsUpdate = true;
    this.geometry.attributes.prev.needsUpdate = true;
    this.geometry.attributes.next.needsUpdate = true;
  };

  this.updateGeometry();

  this.update = (time) => {
    // this.material.uniforms.uColor.value = new THREE.Color(
    //   ...palette(i / parameters.count.lines + time * 0.5)
    // );
    // this.material.uniforms.uColor2.value = new THREE.Color(
    //   ...palette(i / parameters.count.lines + time * 0.5 + 0.2)
    // );

    // // Update polyline input points
    // for (let i = this.points.length - 1; i >= 0; i--) {
    //   if (!i) {
    //     // For the first point, spring ease it to the mouse position
    //     this.tmp.copy(mouse).sub(this.points[i]).multiplyScalar(this.spring);
    //     this.mouseVelocity.add(this.tmp).multiplyScalar(this.friction);
    //     this.points[i].add(this.mouseVelocity);
    //   } else {
    //     // The rest of the points ease to the point in front of them, making a line
    //     // this.points[i].lerp(this.points[i - 1], 0.75);
    //     this.points[i].lerp(this.points[i - 1], 0.75);
    //   }
    // }

    for (let i = this.points.length - 1; i >= 0; i--) {
      if (!i) {
        // For the first point, spring ease it to the mouse position
        this.tmp.copy(mouse).sub(this.points[i]);
        this.points[i].add(this.tmp);
      } else {
        // The rest of the points ease to the point in front of them, making a line
        this.points[i].lerp(this.points[i - 1], 0.75);
      }
    }

    this.updateGeometry();
  };
}

const setLines = () => {
  if (lines.length > 0) {
    lines.forEach((line) => {
      line.geometry.dispose();
      line.material.dispose();
      scene.remove(line.line);
    });
  }

  lines = [];

  for (let i = 0; i < parameters.count.lines; i++) {
    lines.push(new Line(i));
  }
  console.log(lines);
};

setLines();

