// Camera
const frustumSize = 2.0;
const aspect = sizes.width / sizes.height;
const orthoCamera = new THREE.OrthographicCamera(
  (frustumSize * aspect) / -2,
  (frustumSize * aspect) / 2,
  frustumSize / 2,
  frustumSize / -2,
  -1000,
  1000
);

scene.add(orthoCamera);

// Renderer
const renderer = new THREE.WebGLRenderer({
  canvas: canvas,
  // antialias: true,
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
// renderer.setClearColor(0xff0000);

const composer = new EffectComposer(renderer);

const renderPass = new RenderPass(scene, orthoCamera);
composer.addPass(renderPass);

// Displacement pass
const DisplacementShader = {
  uniforms: {
    tDiffuse: { value: null },
    uImage: { value: null },
    uTime: { value: 0 },
  },
  transparent: true,
  vertexShader: vertexPass,
  fragmentShader: fragmentPass,
};

const displacementPass = new ShaderPass(DisplacementShader);
displacementPass.material.uniforms.uImage.value = textureLoader.load(
  "./textures/melt.png"
);
// displacementPass.enabled = false
composer.addPass(displacementPass);

// Animate
const clock = new THREE.Clock();

const tick = () => {
  const elapsedTime = clock.getElapsedTime();

  stats.begin();

  displacementPass.material.uniforms.uTime.value = elapsedTime;

  if (lines.length > 0) {
    lines.forEach((line) => line.update(elapsedTime));
  }

  // Render
  // renderer.render(scene, orthoCamera);
  composer.render();

  // Call tick again on the next frame
  window.requestAnimationFrame(tick);

  stats.end();
};

tick();
